
body, html {
    height: 100vh;
    overflow-y: hidden;
}
#app {
  margin: 0;
  padding: 0;
  top: 0;
  left: 0;
  /*height: 100vw;*/
  height: 100%;
  /*overflow-y: hidden;*/
}
#searchContainer {
    border-right: solid 1px #2ecc71;
}
#homeButton {
    padding: 0;
    margin: 0;
    margin-left: 0.5em;
    cursor: pointer;
    color: #2ecc71;
}
#currentTopic {
    margin: 0;
    margin-left: 3em;
}
#listBox {
    border-right: solid 1px #2ecc71;
    padding: 10px;
    overflow-y: auto;
}
#contentBox {
    /*padding: 0;*/
    overflow-x: auto;
}
.content {
    /*padding: 5px;*/
    height: 100%;
}
.panel {
    height: 3em;
    padding: 3px;
    padding-left: 0;
    padding-right: 0;
    /*border-bottom:*/
    box-shadow: 0px 1px 15px -1px rgba(0,0,0,0.24);
}



